import axios from 'axios';

import blogUrl from '../helpers/BlogUrl';
import loadTemplateContent from '../helpers/TemplateContentLoader';
import { Post } from '../classes/post';

const template = loadTemplateContent('#ddd-wp-posts-view-template');

export default {
    template,

    data() {
        return {
            post: {
                categories: [],
                tags: [],
            },
        };
    },

    mounted() {
        if (this.$route.params.post) {
            this.post = this.$route.params.post;
        } else {
            this.loadPost();
        }
    },

    methods: {
        loadPost() {
            axios.get(`${blogUrl}/wp-json/wp/v2/posts?slug=${this.$route.params.slug}&_embed`)
                .then(this.setPost)
                .catch(console.error);
        },

        setPost({ data: posts }) {
            this.post = new Post(posts[0]);
        },
    },
};
