import getHtmlData from '../helpers/HtmlData';
import GenericPostsComponent from '../helpers/GenericPostsComponent';

const template = 'ddd-wp-recent-posts-template';
const postsPerPage = getHtmlData(document.getElementById(template), 'postCount', 5);

export default GenericPostsComponent(template, queryBuilder => queryBuilder.setPerPage(postsPerPage));
