import axios from 'axios';

import blogUrl from '../helpers/BlogUrl';
import loadTemplateContent from '../helpers/TemplateContentLoader';

const template = loadTemplateContent('#ddd-wp-authors-index-template');

export default {
    template,

    data() {
        return {
            authors: [],
        };
    },

    mounted() {
        this.loadAuthors();
    },

    methods: {
        loadAuthors() {
            axios.get(`${blogUrl}/wp-json/wp/v2/users?per_page=100`)
                .then(this.setAuthors)
                .catch(console.error);
        },

        setAuthors({ data: authors }) {
            this.authors = authors;
        },
    },
};
