import moment from 'moment';

export function Post(original) {
    this.original = original;

    const getTermsByTaxonomy = (taxonomy) => {
        return this.original._embedded['wp:term']
            .flatMap(terms => terms)
            .filter(term => term.taxonomy === taxonomy)
    };

    this.id = this.original.id;
    this.slug = this.original.slug;
    this.title = this.original.title.rendered;
    this.date = moment(this.original.date, moment.ISO_8601, true);
    this.excerpt = this.original.excerpt.rendered;
    this.content = this.original.content.rendered;
    this.author = this.original._embedded.author.map(author => author.name).join(' & ');
    this.categories = getTermsByTaxonomy('category');
    this.tags = getTermsByTaxonomy('post_tag');
};
