export default function (templateId) {
    const idWithoutHash = templateId.charAt(0) === '#' ? templateId.substr(1) : templateId

    const element = document.getElementById(idWithoutHash);

    if (!element) {
        return '<div><!-- Template not provided --></div>';
    }

    return document.getElementById(idWithoutHash)
        .innerHTML.replace('// <![CDATA[', '').replace('// ]]>', '')
};
