import { element as appElement } from './AppElement';
import getHtmlData from './HtmlData';

const urlsToRouteInput = document.getElementById('ddd-wp-vue-app-url-regex');
const urlsToRoute = getHtmlData(appElement, 'urlRegex', urlsToRouteInput ? urlsToRouteInput.value : null);

export default urlsToRoute && (new RegExp(urlsToRoute).test(window.location.href));
