import axios from 'axios';

import { QueryBuilder } from '../helpers/QueryBuilder';
import loadTemplateContent from '../helpers/TemplateContentLoader';
import { Post } from '../classes/post';

export default (
    templateId,
    urlBuilder = undefined,
) => {
    const template = loadTemplateContent(templateId);
    const urlQueryBuilder = new QueryBuilder();
    const urlBuilderFunc = urlBuilder || (queryBuilder => queryBuilder);

    return {
        template,

        props: {
            include: {
                type: Array,
                default: () => [],
            },
            exclude: {
                type: Array,
                default: () => [],
            },
        },

        data() {
            return {
                posts: [],
                currentPage: 1,
                totalPages: 1,
            };
        },

        computed: {
            pages() {
                return [...Array(this.totalPages).keys()].map(i => i + 1);
            },
        },

        mounted() {
            this.loadPosts();
        },

        methods: {
            loadPosts() {
                urlQueryBuilder
                    .excludeCategories(this.exclude)
                    .getPage(this.currentPage);

                if (this.include.length > 0) {
                    urlQueryBuilder.includeCategories(this.include);
                }

                const fullUrl = urlBuilderFunc.apply(this, [urlQueryBuilder]).build();

                axios.get(fullUrl)
                    .then(this.setPagination)
                    .then(this.setPosts)
                    .catch(console.error);
            },

            setPosts({ data: rawPosts }) {
                this.posts = rawPosts.map(post => new Post(post));
            },

            setPagination(response) {
                this.totalPages = parseInt(response.headers['x-wp-totalpages']);

                return response;
            },

            goToPage(page) {
                if (page === this.currentPage) {
                    return;
                }

                window.scrollTo({ top: 0, left: 0, behavior: 'smooth'});
                this.currentPage = page;
                this.loadPosts();
            },

            previousPage() {
                const previous = this.currentPage - 1;

                if (previous >= 1) {
                    this.goToPage(previous);
                }
            },

            nextPage() {
                const next = this.currentPage + 1;

                if (next <= this.totalPages) {
                    this.goToPage(next);
                }
            },
        },
    };
};
