import Vue from 'vue';
import VueRouter from 'vue-router';

import '../assets/app.scss';

import { element as el } from './helpers/AppElement';
import shouldRun from './helpers/ShouldRun';

import router from './router';
import AuthorsIndex from './components/AuthorsIndex';
import CategoriesIndex from './components/CategoriesIndex';
import RecentPosts from './components/RecentPosts';

if (shouldRun) {
    const components = {
        'ddd-wp-authors-index': AuthorsIndex,
        'ddd-wp-categories-index': CategoriesIndex,
        'ddd-wp-recent-posts': RecentPosts,
    };

    Vue.use(VueRouter);
    new Vue({ el, router, components });
}
