import blogUrl from './BlogUrl';

const categoryAndTagQueryStringifier = (catOrTag, included, excluded, prefix) => {
    let includeQuery = included.length > 0
        ? `${catOrTag}[]=` + included.join(`&${catOrTag}[]=`)
        : '';

    // Don't exclude, if specifically included
    const actuallyExclude = excluded.filter(id => !included.includes(id));
    const excludeQuery = actuallyExclude.length > 0
        ? `${catOrTag}_exclude=` + actuallyExclude.join(`&${catOrTag}_exclude=`)
        : '';

    if (includeQuery.length > 0 && excludeQuery.length > 0) {
        includeQuery = `${includeQuery}&`;
    }

    const query = `${includeQuery}${excludeQuery}`;

    return prefix && query.length > 0 ? `&${query}` : query;
};

const QueryBuilder = function () {
    let authorsIncluded = [];
    let authorsExcluded = [];

    this.includeAuthors = (authorIds) => {
        authorsIncluded = authorIds.map(parseInt);
        return this;
    };
    this.excludeAuthors = (authorIds) => {
        authorsExcluded = authorIds.map(parseInt);
        return this;
    };

    let categoriesIncluded = [];
    let categoriesExcluded = [];

    this.includeCategories = (categoryIds) => {
        categoriesIncluded = categoryIds.map(parseInt);
        return this;
    };
    this.excludeCategories = (categoryIds) => {
        categoriesExcluded = categoryIds.map(parseInt);
        return this;
    };

    let tagsIncluded = [];
    let tagsExcluded = [];

    this.includeTags = (tagIds) => {
        tagsIncluded = tagIds.map(parseInt);
        return this;
    };
    this.excludeTags = (tagIds) => {
        tagsExcluded = tagIds.map(parseInt);
        return this;
    };

    let perPage = 10;
    let page = 1;

    this.setPerPage = (value) => {
        perPage = value;
        return this;
    };

    this.getPage = (value) => {
        page = value;
        return this;
    };

    this.build = (taxonomy = 'posts') => {
        const authorQuery = categoryAndTagQueryStringifier('author', authorsIncluded, authorsExcluded, true);
        const categoryQuery = categoryAndTagQueryStringifier('categories', categoriesIncluded, categoriesExcluded, true);
        const tagQuery = categoryAndTagQueryStringifier('tags', tagsIncluded, tagsExcluded, true);

        const query = `per_page=${perPage}&page=${page}${authorQuery}${categoryQuery}${tagQuery}`;

        return `${blogUrl}/wp-json/wp/v2/${taxonomy}?${query}&_embed=1`;
    };
}

export { QueryBuilder };
