import GenericPostsComponent from '../helpers/GenericPostsComponent';

const templateId = 'ddd-wp-authors-view-template';
const template = document.getElementById(templateId) ? templateId : 'ddd-wp-posts-index-template';

export default GenericPostsComponent(template, function (queryBuilder) {
    return queryBuilder.excludeAuthors([]).includeAuthors([this.$route.params.id]);
}, false, true);
