import VueRouter from 'vue-router';

import shouldRun from '../helpers/ShouldRun';
import routes from './routes';

// Export empty object if not running so that we don't get # in the URL
const router = shouldRun ? new VueRouter({ routes }) : {};

router.afterEach((to) => {
  if (window.ga) {
    const page = window.location.pathname + '#' + to.path;
    const allTrackers = ga.getAll();

    if (allTrackers.length) {
      allTrackers.forEach(tracker => tracker.send('pageview', page))
    } else {
      window.ga('send', 'pageview', page);
    }
  }
});

export default router;
