import axios from 'axios';

import blogUrl from '../helpers/BlogUrl';
import loadTemplateContent from '../helpers/TemplateContentLoader';

const template = loadTemplateContent('#ddd-wp-categories-index-template');

export default {
    template,

    data() {
        return {
            categories: [],
        };
    },

    mounted() {
        this.loadCategories();
    },

    methods: {
        loadCategories() {
            axios.get(`${blogUrl}/wp-json/wp/v2/categories`)
                .then(this.setCategories)
                .catch(console.error);
        },

        setCategories({ data: categories }) {
            this.categories = categories;
        },
    },
};
